<template><section><h1>radio</h1>
<template>
	<coding :code="init" title="简单的radio" content="跟原生的radio一样，只需要在组件上绑定v-model。">
		<y-radio label="你是智障" v-model="test">你是智障</y-radio>
		<y-radio label="你不是智障" v-model="test">你不是智障</y-radio>
		<span class="data">data:{{test}}</span>
	</coding>
	<coding :code="disabled" title="禁用，默认" content="可以使用checked来指定默认选项，使用disabled来禁用radio">
		<y-radio label="1" v-model="test1" checked disabled>disabled</y-radio>
		<y-radio label="2" v-model="test1" disabled>disabled</y-radio>
	</coding>
	<coding :code="group" title="group" content="使用group">
		<y-radio-group v-model="test2">
			<y-radio label="1">1</y-radio>
			<y-radio label="2">2</y-radio>
			<y-radio label="3">3</y-radio>
			<y-radio label="4">4</y-radio>
		</y-radio-group>
	</coding>
	<coding :code="btn" title="单选按钮" content="单选按钮，跟radio一样，也可以禁用，设置默认。">
		<y-radio-group v-model="test3">
			<y-radio-button label="1" checked>disabled</y-radio-button>
			<y-radio-button label="2" disabled>disabled</y-radio-button>
			<y-radio-button label="4">disabled</y-radio-button>
			<y-radio-button label="3">disabled</y-radio-button>
		</y-radio-group>
	</coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>checked</td>
<td>默认选项</td>
<td>-</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>-</td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
let init =
`<template>
	<y-radio label="你是智障" v-model="test">你是智障</y-radio>
	<y-radio label="你不是智障" v-model="test">你不是智障</y-radio>
</template>
`
let disabled =
`<template>
	<y-radio label="1" v-model="test1" checked disabled>disabled</y-radio>
	<y-radio label="2" v-model="test1" disabled>disabled</y-radio>
</template>`
let group =
`<template>
	<y-radio-group v-model="test2">
		<y-radio label="1" >1</y-radio>
		<y-radio label="2" >2</y-radio>
		<y-radio label="3" >3</y-radio>
		<y-radio label="4" >4</y-radio>
	</y-radio-group>
</template>
`
let btn =
`<template>
	<y-radio-group v-model="test3">
		<y-radio-button label="1" checked>disabled</y-radio-button>
		<y-radio-button label="2" disabled>disabled</y-radio-button>
		<y-radio-button label="4">disabled</y-radio-button>
		<y-radio-button label="3" >disabled</y-radio-button>
	</y-radio-group>
</template>
`
export default {
	data(){
		return {
			test:"你不是智障",
			test1:"1",
			test2:"",
			test3:"",
			init:init,
			disabled:disabled,
			group:group,
			btn:btn
		}
	}
}
</script>


